import UserQualification from "./userQualification-model.js";
const userQualification = new UserQualification();
export default {
	data() {
		return {
			userInfo: {},
			info: {}
		};
	},
	onLoad() {
		const that = this
		that._onLoad()
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.userInfo = that.$store.state.userInfo;
			console.log(that.userInfo)
			that.getDetails(() => {
				callBack && callBack();
			})
		},
		// 详情加载
		getDetails(callBack) {
			const that = this
			userQualification.getDetails({
				openid: that.userInfo.openid,
				id: that.userInfo.id
			}, (res) => {
				console.log(res)
				if (res.code == 4000) {
					that.info = res.data
					userQualification.show_tips(res.explain)
				}
				callBack && callBack();
			})
		},
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		// let shareData = {
		// 	title: '',
		// 	path: `pages/index/index`,
		// 	imageUrl: ''
		// }
		return userQualification.onShareAppMessage({});
	}
}
